<template>
	<view class="deal">
		<!-- 顶部导航 -->
		<watermark></watermark>
		<navTabs @getData="_getData" @clickPrj="_clickPrj"></navTabs>
		<view class="tabs_item paddingBottom">
			<view class="express_box">
				<pieEcharts :Column="chartData"></pieEcharts>
			</view>
			<view class="express_box">
				<view class="express_list_wrap">
					<view class="express_list" v-for="(item,index) in tdList" :key="index">
						<view class="proIndex">{{index + 1}}</view>
						<view class="right_box">
							<view class="proName">{{item.projectName}}</view>
							<view class="bottom">
								<view class="bottom_item">
									<text>未跟进</text>
									<text>{{item.followUpOverdue}}</text>
								</view>
								<view class="bottom_item">
									<text>未签约</text>
									<text>{{item.signOverdue}}</text>
								</view>
								<view class="bottom_item">
									<text>未认购</text>
									<text>{{item.subscribeOverdue}}</text>
								</view>
								<view class="bottom_item">
									<text>总逾期</text>
									<text>{{item.totalOverdue}}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
				<!-- <utable :thList="thList" :tdList="tdList" :status="14"></utable> -->
			</view>
		</view>
		<!-- <view class="footer" @click="goBack">返回</view> -->
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import navTabs from '@/components/nav-tabs.vue';
	import pieEcharts from '@/components/echartsComponent/pieEcharts.vue';
	import utable from '@/components/utable.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				form: {
					companyId:'', // 公司id
					projectId:'', // 项目id
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				thList:[{name:'项目'},{name:'未跟进'},{name:'未签约'},{name:'未认购'},{name:'总逾期'}],
				tdList:[],
				chartData: {
				  series: []
				}
			}
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current == 0 ? 7 : 8
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3011', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.chartData = res.data.data.pieData
						that.tdList = that.etype == 3 ? that.tdList.concat(res.data.data.tableData.data) : res.data.data.tableData.data
						that.totalPages = res.data.data.tableData.totalPage
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
						bus.$emit('changePie',that.chartData)
					}
				})
			},
			// 点击公司回调
			_getData(val, val2){
				this.form.companyId = val
				this.form.projectId = val2
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 点击项目回调
			_clickPrj(val){
				this.form.projectId = val
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.flag = true
					this.getList()
				}
			},
			// 返回
			// goBack(){
			// 	uni.navigateBack({
			// 		delta:1
			// 	})
			// }
		},
		components: {
			watermark,
			navTabs,
			pieEcharts,
			utable
		},
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
	.tabs_item{
		margin-top: 100rpx;
	}
</style>
